"use client";

import { useState, useEffect } from "react";
import { useAnime } from "@/context/animeContext";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { searchAnimeInfo, analyzeAnimeForRating } from "@/lib/gemini";
import { toast } from "sonner";
import Link from "next/link";
import type { Anime } from "@/context/animeContext";

export default function AnimePage() {
  const { animes, addAnime, updateAnime, deleteAnime, getAnimesByStatus, getTopRatedAnimes } = useAnime();
  const [searchQuery, setSearchQuery] = useState("");
  const [filteredAnimes, setFilteredAnimes] = useState(animes);
  const [selectedStatus, setSelectedStatus] = useState<string>("all");
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false);
  const [isSearching, setIsSearching] = useState(false);
  const [isAnalyzing, setIsAnalyzing] = useState(false);

  // Form state per aggiungere anime
  const [newAnime, setNewAnime] = useState({
    title: "",
    description: "",
    genre: [] as string[],
    status: "plan-to-watch" as Anime['status'],
    rating: undefined as number | undefined,
    episodes: undefined as number | undefined,
    currentEpisode: undefined as number | undefined,
    releaseYear: undefined as number | undefined,
    studio: "",
    notes: ""
  });

  // Filtra gli anime in base alla ricerca e allo status
  useEffect(() => {
    let filtered = animes;

    if (selectedStatus !== "all") {
      filtered = getAnimesByStatus(selectedStatus as Anime['status']);
    }

    if (searchQuery.trim()) {
      const query = searchQuery.toLowerCase();
      filtered = filtered.filter(anime =>
        anime.title.toLowerCase().includes(query) ||
        anime.description.toLowerCase().includes(query) ||
        anime.genre.some(g => g.toLowerCase().includes(query)) ||
        anime.studio?.toLowerCase().includes(query)
      );
    }

    setFilteredAnimes(filtered);
  }, [animes, searchQuery, selectedStatus, getAnimesByStatus]);

  const handleSearchAnime = async () => {
    if (!newAnime.title.trim()) {
      toast.error("Inserisci il titolo dell'anime");
      return;
    }

    setIsSearching(true);
    try {
      const animeInfo = await searchAnimeInfo(newAnime.title);
      if (animeInfo) {
        setNewAnime(prev => ({
          ...prev,
          description: animeInfo.description || prev.description,
          genre: animeInfo.genre || prev.genre,
          episodes: animeInfo.episodes || prev.episodes,
          releaseYear: animeInfo.releaseYear || prev.releaseYear,
          studio: animeInfo.studio || prev.studio,
        }));
        toast.success("Informazioni anime trovate!");
      } else {
        toast.warning("Anime non trovato, compila manualmente");
      }
    } catch (error) {
      toast.error("Errore nella ricerca");
      console.error("Errore ricerca:", error);
    } finally {
      setIsSearching(false);
    }
  };

  const handleAnalyzeAnime = async () => {
    if (!newAnime.title.trim()) {
      toast.error("Inserisci il titolo dell'anime");
      return;
    }

    setIsAnalyzing(true);
    try {
      const analysis = await analyzeAnimeForRating(newAnime.title, newAnime.notes);
      setNewAnime(prev => ({
        ...prev,
        rating: analysis.suggestedRating,
        genre: [...new Set([...prev.genre, ...analysis.tags])],
      }));
      toast.success(`Voto suggerito: ${analysis.suggestedRating}/10`);
    } catch (error) {
      toast.error("Errore nell'analisi");
      console.error("Errore analisi:", error);
    } finally {
      setIsAnalyzing(false);
    }
  };

  const handleAddAnime = () => {
    if (!newAnime.title.trim() || !newAnime.description.trim()) {
      toast.error("Titolo e descrizione sono obbligatori");
      return;
    }

    try {
      addAnime(newAnime);
      toast.success("Anime aggiunto alla collezione!");
      setIsAddDialogOpen(false);
      setNewAnime({
        title: "",
        description: "",
        genre: [],
        status: "plan-to-watch",
        rating: undefined,
        episodes: undefined,
        currentEpisode: undefined,
        releaseYear: undefined,
        studio: "",
        notes: ""
      });
    } catch (error) {
      toast.error("Errore nell'aggiungere l'anime");
      console.error("Errore:", error);
    }
  };

  const getStatusColor = (status: Anime['status']) => {
    switch (status) {
      case 'completed': return 'bg-green-500';
      case 'watching': return 'bg-blue-500';
      case 'plan-to-watch': return 'bg-yellow-500';
      case 'on-hold': return 'bg-orange-500';
      case 'dropped': return 'bg-red-500';
      default: return 'bg-gray-500';
    }
  };

  const getStatusText = (status: Anime['status']) => {
    switch (status) {
      case 'completed': return 'Completato';
      case 'watching': return 'In Visione';
      case 'plan-to-watch': return 'Da Guardare';
      case 'on-hold': return 'In Pausa';
      case 'dropped': return 'Abbandonato';
      default: return status;
    }
  };

  const topRated = getTopRatedAnimes(5);

  return (
    <div className="max-w-7xl mx-auto">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-foreground mb-2">I Miei Anime</h1>
          <p className="text-muted-foreground">
            {animes.length} anime nella tua collezione
          </p>
        </div>
        
        <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
          <DialogTrigger asChild>
            <Button className="mt-4 sm:mt-0">
              Aggiungi Anime
            </Button>
          </DialogTrigger>
          <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>Aggiungi Nuovo Anime</DialogTitle>
              <DialogDescription>
                Cerca informazioni online o compila manualmente
              </DialogDescription>
            </DialogHeader>
            
            <div className="space-y-4">
              {/* Titolo e ricerca */}
              <div>
                <Label htmlFor="title">Titolo *</Label>
                <div className="flex gap-2 mt-1">
                  <Input
                    id="title"
                    value={newAnime.title}
                    onChange={(e) => setNewAnime(prev => ({ ...prev, title: e.target.value }))}
                    placeholder="Es: Attack on Titan"
                  />
                  <Button 
                    variant="outline" 
                    onClick={handleSearchAnime}
                    disabled={isSearching}
                  >
                    {isSearching ? "Cercando..." : "Cerca"}
                  </Button>
                </div>
              </div>

              {/* Descrizione */}
              <div>
                <Label htmlFor="description">Descrizione *</Label>
                <Textarea
                  id="description"
                  value={newAnime.description}
                  onChange={(e) => setNewAnime(prev => ({ ...prev, description: e.target.value }))}
                  placeholder="Breve descrizione dell'anime..."
                  rows={3}
                />
              </div>

              {/* Status e Rating */}
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="status">Status</Label>
                  <Select value={newAnime.status} onValueChange={(value) => setNewAnime(prev => ({ ...prev, status: value as Anime['status'] }))}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="plan-to-watch">Da Guardare</SelectItem>
                      <SelectItem value="watching">In Visione</SelectItem>
                      <SelectItem value="completed">Completato</SelectItem>
                      <SelectItem value="on-hold">In Pausa</SelectItem>
                      <SelectItem value="dropped">Abbandonato</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                
                <div>
                  <Label htmlFor="rating">Voto (1-10)</Label>
                  <div className="flex gap-2">
                    <Input
                      id="rating"
                      type="number"
                      min="1"
                      max="10"
                      step="0.1"
                      value={newAnime.rating || ""}
                      onChange={(e) => setNewAnime(prev => ({ ...prev, rating: e.target.value ? parseFloat(e.target.value) : undefined }))}
                      placeholder="8.5"
                    />
                    <Button 
                      variant="outline" 
                      size="sm"
                      onClick={handleAnalyzeAnime}
                      disabled={isAnalyzing}
                    >
                      {isAnalyzing ? "..." : "AI"}
                    </Button>
                  </div>
                </div>
              </div>

              {/* Episodi e Anno */}
              <div className="grid grid-cols-3 gap-4">
                <div>
                  <Label htmlFor="episodes">Episodi</Label>
                  <Input
                    id="episodes"
                    type="number"
                    value={newAnime.episodes || ""}
                    onChange={(e) => setNewAnime(prev => ({ ...prev, episodes: e.target.value ? parseInt(e.target.value) : undefined }))}
                    placeholder="24"
                  />
                </div>
                
                <div>
                  <Label htmlFor="currentEpisode">Episodio Attuale</Label>
                  <Input
                    id="currentEpisode"
                    type="number"
                    value={newAnime.currentEpisode || ""}
                    onChange={(e) => setNewAnime(prev => ({ ...prev, currentEpisode: e.target.value ? parseInt(e.target.value) : undefined }))}
                    placeholder="12"
                  />
                </div>
                
                <div>
                  <Label htmlFor="releaseYear">Anno</Label>
                  <Input
                    id="releaseYear"
                    type="number"
                    value={newAnime.releaseYear || ""}
                    onChange={(e) => setNewAnime(prev => ({ ...prev, releaseYear: e.target.value ? parseInt(e.target.value) : undefined }))}
                    placeholder="2023"
                  />
                </div>
              </div>

              {/* Studio */}
              <div>
                <Label htmlFor="studio">Studio</Label>
                <Input
                  id="studio"
                  value={newAnime.studio}
                  onChange={(e) => setNewAnime(prev => ({ ...prev, studio: e.target.value }))}
                  placeholder="Es: MAPPA, Wit Studio"
                />
              </div>

              {/* Generi */}
              <div>
                <Label htmlFor="genre">Generi (separati da virgola)</Label>
                <Input
                  id="genre"
                  value={newAnime.genre.join(", ")}
                  onChange={(e) => setNewAnime(prev => ({ ...prev, genre: e.target.value.split(",").map(g => g.trim()).filter(g => g) }))}
                  placeholder="azione, avventura, drammatico"
                />
              </div>

              {/* Note */}
              <div>
                <Label htmlFor="notes">Note Personali</Label>
                <Textarea
                  id="notes"
                  value={newAnime.notes}
                  onChange={(e) => setNewAnime(prev => ({ ...prev, notes: e.target.value }))}
                  placeholder="Le tue impressioni sull'anime..."
                  rows={2}
                />
              </div>

              <div className="flex gap-2 pt-4">
                <Button onClick={handleAddAnime} className="flex-1">
                  Aggiungi alla Collezione
                </Button>
                <Button variant="outline" onClick={() => setIsAddDialogOpen(false)}>
                  Annulla
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {/* Filtri e Ricerca */}
      <div className="flex flex-col sm:flex-row gap-4 mb-8">
        <Input
          placeholder="Cerca nei tuoi anime..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="flex-1"
        />
        <Select value={selectedStatus} onValueChange={setSelectedStatus}>
          <SelectTrigger className="w-full sm:w-48">
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="all">Tutti gli Status</SelectItem>
            <SelectItem value="watching">In Visione</SelectItem>
            <SelectItem value="completed">Completati</SelectItem>
            <SelectItem value="plan-to-watch">Da Guardare</SelectItem>
            <SelectItem value="on-hold">In Pausa</SelectItem>
            <SelectItem value="dropped">Abbandonati</SelectItem>
          </SelectContent>
        </Select>
      </div>

      <Tabs defaultValue="grid" className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="grid">Griglia</TabsTrigger>
          <TabsTrigger value="list">Lista</TabsTrigger>
          <TabsTrigger value="stats">Statistiche</TabsTrigger>
        </TabsList>
        
        <TabsContent value="grid" className="mt-6">
          {filteredAnimes.length > 0 ? (
            <div className="grid md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
              {filteredAnimes.map((anime) => (
                <Card key={anime.id} className="hover:shadow-lg transition-shadow">
                  <CardHeader>
                    <div className="flex justify-between items-start">
                      <CardTitle className="line-clamp-2 text-lg">{anime.title}</CardTitle>
                      <Badge className={`${getStatusColor(anime.status)} text-white text-xs`}>
                        {getStatusText(anime.status)}
                      </Badge>
                    </div>
                    <CardDescription className="flex items-center gap-2">
                      {anime.releaseYear && <span>{anime.releaseYear}</span>}
                      {anime.studio && <span>• {anime.studio}</span>}
                    </CardDescription>
                  </CardHeader>
                  <CardContent>
                    <p className="text-sm text-muted-foreground line-clamp-3 mb-4">
                      {anime.description}
                    </p>
                    
                    {/* Rating e Progresso */}
                    <div className="flex justify-between items-center mb-3">
                      {anime.rating && (
                        <div className="flex items-center gap-1">
                          <span className="text-yellow-500">★</span>
                          <span className="font-medium">{anime.rating}/10</span>
                        </div>
                      )}
                      {anime.episodes && (
                        <span className="text-sm text-muted-foreground">
                          {anime.currentEpisode || 0}/{anime.episodes} ep
                        </span>
                      )}
                    </div>
                    
                    {/* Generi */}
                    {anime.genre.length > 0 && (
                      <div className="flex flex-wrap gap-1 mb-3">
                        {anime.genre.slice(0, 3).map((genre, index) => (
                          <Badge key={index} variant="secondary" className="text-xs">
                            {genre}
                          </Badge>
                        ))}
                        {anime.genre.length > 3 && (
                          <Badge variant="outline" className="text-xs">
                            +{anime.genre.length - 3}
                          </Badge>
                        )}
                      </div>
                    )}
                    
                    <div className="flex gap-2">
                      <Link href={`/anime/${anime.id}`} className="flex-1">
                        <Button variant="outline" size="sm" className="w-full">
                          Dettagli
                        </Button>
                      </Link>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          ) : (
            <div className="text-center py-16">
              <h3 className="text-xl font-semibold mb-2">Nessun Anime Trovato</h3>
              <p className="text-muted-foreground mb-6">
                {searchQuery || selectedStatus !== "all" 
                  ? "Prova a modificare i filtri di ricerca"
                  : "Inizia aggiungendo il tuo primo anime alla collezione!"
                }
              </p>
              <Button onClick={() => setIsAddDialogOpen(true)}>
                Aggiungi Primo Anime
              </Button>
            </div>
          )}
        </TabsContent>
        
        <TabsContent value="list" className="mt-6">
          {/* Vista lista - implementazione semplificata */}
          <div className="space-y-4">
            {filteredAnimes.map((anime) => (
              <Card key={anime.id}>
                <CardContent className="pt-6">
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <h3 className="font-semibold text-lg">{anime.title}</h3>
                      <p className="text-muted-foreground text-sm">{anime.description}</p>
                      <div className="flex items-center gap-4 mt-2">
                        <Badge className={`${getStatusColor(anime.status)} text-white`}>
                          {getStatusText(anime.status)}
                        </Badge>
                        {anime.rating && (
                          <span className="text-sm">★ {anime.rating}/10</span>
                        )}
                        {anime.episodes && (
                          <span className="text-sm text-muted-foreground">
                            {anime.currentEpisode || 0}/{anime.episodes} ep
                          </span>
                        )}
                      </div>
                    </div>
                    <Link href={`/anime/${anime.id}`}>
                      <Button variant="outline" size="sm">
                        Dettagli
                      </Button>
                    </Link>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>
        
        <TabsContent value="stats" className="mt-6">
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
            {/* Statistiche generali */}
            <Card>
              <CardHeader>
                <CardTitle>Statistiche Generali</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="flex justify-between">
                    <span>Totale Anime:</span>
                    <span className="font-medium">{animes.length}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Completati:</span>
                    <span className="font-medium">{getAnimesByStatus('completed').length}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>In Visione:</span>
                    <span className="font-medium">{getAnimesByStatus('watching').length}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Da Guardare:</span>
                    <span className="font-medium">{getAnimesByStatus('plan-to-watch').length}</span>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Top Rated */}
            {topRated.length > 0 && (
              <Card>
                <CardHeader>
                  <CardTitle>I Tuoi Preferiti</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    {topRated.map((anime, index) => (
                      <div key={anime.id} className="flex justify-between items-center">
                        <span className="text-sm truncate">{index + 1}. {anime.title}</span>
                        <span className="text-sm font-medium">★ {anime.rating}</span>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            )}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}
